import React, { useRef, useState, useEffect } from 'react';
import axios from 'axios';
import * as faceapi from 'face-api.js';
import './App.css';

function App() {
    const videoRef = useRef(null);
    const [scanning, setScanning] = useState(false);
    const [result, setResult] = useState(null);
    const [faceDetected, setFaceDetected] = useState(false);

    useEffect(() => {
        const loadModels = async () => {
          await faceapi.nets.tinyFaceDetector.loadFromUri('/models');
          startVideo();
        };
        loadModels();
    }, );

    const startVideo = async () => {
        const stream = await navigator.mediaDevices.getUserMedia({ video: true });
        videoRef.current.srcObject = stream;
        videoRef.current.play();  // Ajoutez cette ligne
        videoRef.current.onloadedmetadata = () => {
            detectFace();
        };
    };
  

    const detectFace = async () => {
        const options = new faceapi.TinyFaceDetectorOptions({ inputSize: 160 });
        const result = await faceapi.detectSingleFace(videoRef.current, options);
        if (result) {
            setFaceDetected(true);
        } else {
            setFaceDetected(false);
        }
        setTimeout(detectFace, 100);
    };

    const captureImage = async () => {
        setScanning(true);
        const canvas = document.createElement('canvas');
        canvas.width = videoRef.current.videoWidth;
        canvas.height = videoRef.current.videoHeight;
        canvas.getContext('2d').drawImage(videoRef.current, 0, 0);
        videoRef.current.srcObject.getTracks().forEach(track => track.stop());

        canvas.toBlob(async blob => {
            const formData = new FormData();
            formData.append('image', blob);
            try {
                const response = await axios.post('http://localhost:5000/process', formData);
                setResult(response.data);
            } catch (error) {
                console.error("Error sending the image:", error);
            }
            setScanning(false);
        });
    };

    return (
      <div className="app-container">
        <h1 className="app-title">Face Detection App</h1>
        <div className="video-container">
            <video ref={videoRef} width="720" height="560"></video>
            {scanning && <div className="scanning-overlay">Scanning...</div>}
            <div className={`face-frame ${faceDetected ? 'green' : 'red'}`}></div>
        </div>
        <button onClick={captureImage} disabled={scanning || !faceDetected}>Capture</button>
        {result && (
        <div className="result-display">
            <h2>Scan Result</h2>
            <hr />
            <p><strong>Name:</strong> {result.name}</p>
            <p><strong>Presence:</strong> {result.presence}</p>
            <p><strong>Time:</strong> {result.timestamp}</p> 
        </div>
)}

      </div>
  
    );
}

export default App;

